import * as React from "react";
import { Provider } from 'react-redux';
import { createStore } from 'redux';

import { Utils } from '../utils/Utils';

import { RegisterStepBar } from "../components/RegisterStepBar";
import { RegisterInfo } from "../components/RegisterInfo";
import { RegisterAccountValidation } from "../components/RegisterAccountValidation";
import { RegisterFinish } from "../components/RegisterFinish";

import { RegisterReducers } from "../reducers/register.reducer";

let defultInitState : any = {
    registerSetp : 1,
    userName : "",
    password : "",
    confirmPassword : "",
    email : "",
    verification : "",
    userNameError: false ,
    userNameErrorInfo: "",
    passwordError: false,
    passwordErrorInfo: "",
    confirmPasswordError: false,
    confirmPasswordErrorInfo: "",
    emailError: false,
    emailErrorInfo: "",
    verificationError: false,
    verificationErrorInfo: "",
    verificationImgUrl : "src/images/VerifyCode.png",
    userNickName : "",
    userNickNameError : false,
    userNickNmaeErrorInfo : "",
    userPhone : "",
    userPhoneError : false,
    userPhoneErrorInfo : "",
    phoneCode : "",
    phoneCodeError : false,
    phoneCodeErrorInfo : "",
    phoneCodeCountdown : 0,
    isSendPhoneCode : false
}

let store : any = createStore(RegisterReducers.RegisterReducer, defultInitState);
let locationKey : string;

@Utils.subscribeToParentStore(store)
export default class Register extends React.Component<any, any>{


    componentWillReceiveProps(){

    }

    render(){

        if(locationKey !== this.props.location.key){
            store.dispatch({type : "INIT"});
            locationKey = this.props.location.key;
        }

        let registerStepComponent : any;
        let registerStep = store.getState().registerSetp;
        if(registerStep === 1){
            registerStepComponent = <RegisterInfo store={store} />;
        }else if(registerStep === 2){
            registerStepComponent = <RegisterAccountValidation store={store} />;
        }else if(registerStep === 3){
            registerStepComponent = <RegisterFinish store={store} />;
        }

        return (
            <Provider store={store}>
                <div className="content main page register">
                    <div className="row">
                        <div className="form-box">
                            <RegisterStepBar activeindex={ store.getState().registerSetp } />
                            {registerStepComponent}
                        </div>
                    </div>
                </div>
            </Provider>
        );
    }
}




